import React, { useState, useEffect, useRef } from 'react';
import { motion } from 'framer-motion';
import { useNavigate } from 'react-router-dom';
import { useBooking } from '../../contexts/BookingContext';
import { useAuth } from '../../contexts/AuthContext';
//  Stripe 
import axios from 'axios';
import apiConfig from '../../config/api';
import { 
  CalendarDaysIcon, 
  ClockIcon, 
  UsersIcon,
  MapPinIcon,
  CurrencyDollarIcon,
  ExclamationTriangleIcon
} from '@heroicons/react/24/outline';

interface BookingData {
  totalPlayers: number;
  contactName: string;
  contactEmail: string;
  contactPhone: string;
}

interface BookingSummaryProps {
  court: any;
  date: string;
  timeSlot: { start: string; end: string } | null;
  bookingData: BookingData;
  availability: any;
  // 
  onReset: () => void;
  onEditBooking?: (field: keyof BookingData, value: any) => void;
  includeSoloCourt?: boolean;
  soloCourtAvailable?: boolean;
  onToggleSoloCourt?: (include: boolean) => void;
}

const BookingSummary: React.FC<BookingSummaryProps> = ({
  court,
  date,
  timeSlot,
  bookingData,
  availability,
  // 
  onReset,
  onEditBooking,
  includeSoloCourt = false,
  soloCourtAvailable = false,
  onToggleSoloCourt
}) => {
  const { createBooking } = useBooking();
  const { user } = useAuth();
  const navigate = useNavigate();
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [specialRequests, setSpecialRequests] = useState('');
  const [isEditing, setIsEditing] = useState(false);
  const [editData, setEditData] = useState<BookingData>(bookingData);

  // 
  

  //  bookingData  editData
  useEffect(() => {
    setEditData(bookingData);
  }, [bookingData]);

  // 
  const handleEdit = () => {
    setIsEditing(true);
  };

  const handleSave = () => {
    if (onEditBooking) {
      onEditBooking('totalPlayers', editData.totalPlayers);
      onEditBooking('contactName', editData.contactName);
      onEditBooking('contactEmail', editData.contactEmail);
      onEditBooking('contactPhone', editData.contactPhone);
    }
    setIsEditing(false);
  };

  const handleCancel = () => {
    setEditData(bookingData);
    setIsEditing(false);
  };

  const handleEditChange = (field: keyof BookingData, value: any) => {
    setEditData(prev => ({
      ...prev,
      [field]: value
    }));
  };

  const formatTime = (time: string) => {
    const [hour, minute] = time.split(':');
    const hourNum = parseInt(hour);
    const period = hourNum >= 12 ? 'PM' : 'AM';
    const displayHour = hourNum === 0 ? 12 : hourNum > 12 ? hourNum - 12 : hourNum;
    return `${displayHour}:${minute} ${period}`;
  };

  const calculateDuration = () => {
    if (!timeSlot) return 0;
    const start = timeSlot.start.split(':');
    const end = timeSlot.end.split(':');
    const startMinutes = parseInt(start[0]) * 60 + parseInt(start[1]);
    let endMinutes = parseInt(end[0]) * 60 + parseInt(end[1]);
    
    //  24:00 1440 
    if (end[0] === '24' && end[1] === '00') {
      endMinutes = 24 * 60;
    }
    
    return endMinutes - startMinutes;
  };

  //  24:00  00:00
  const normalizeTime = (time: string) => {
    if (time === '24:00') {
      return '00:00';
    }
    return time;
  };

  const handleSubmit = async () => {
    console.log(' handleSubmit ');
    
    if (!user) {
      alert('');
      return;
    }

    if (!court || !date || !timeSlot || !bookingData.contactName) {
      console.log(' :', { court: !!court, date: !!date, timeSlot: !!timeSlot, contactName: !!bookingData.contactName });
      alert('');
      return;
    }

    console.log(' ');
    setIsSubmitting(true);
    try {
      const bookingPayload = {
        court: court._id,
        date,
        startTime: timeSlot.start,
        endTime: normalizeTime(timeSlot.end), //  24:00  00:00
        players: [{ 
          name: bookingData.contactName, 
          email: bookingData.contactEmail, 
          phone: bookingData.contactPhone 
        }],
        totalPlayers: bookingData.totalPlayers,
        specialRequests: specialRequests.trim() || undefined,
        includeSoloCourt: includeSoloCourt || false
      };

      // 
      console.log(' :', {
        includeSoloCourt: includeSoloCourt,
        courtType: court?.type,
        soloCourtAvailable: soloCourtAvailable,
        payload: bookingPayload
      });

      //  1: 
      console.log('  1: ');
      const newBooking = await createBooking(bookingPayload);
      console.log(' :', newBooking);
      
      if (!newBooking._id) {
        throw new Error(' ID');
      }

      //  2: 
      console.log('  2: ');
      
      // 
      alert('');
      window.location.href = '/my-bookings';
    } catch (error: any) {
      console.error(' :', error);
      alert(error.message || '');
    } finally {
      setIsSubmitting(false);
    }
  };

  // 

  if (!court || !date || !timeSlot) {
    return (
      <div className="text-center py-12">
        <ExclamationTriangleIcon className="w-12 h-12 text-yellow-500 mx-auto mb-4" />
        <p className="text-gray-600"></p>
      </div>
    );
  }

  return (
    <div>
      <h2 className="text-2xl font-bold text-gray-900 mb-6"></h2>
      <p className="text-gray-600 mb-8"></p>

      <div className="space-y-6">
        {/*  */}
        <div className="bg-white border border-gray-200 rounded-xl p-6">
          <h3 className="text-lg font-semibold text-gray-900 mb-4"></h3>
          
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div className="flex items-center gap-3">
              <MapPinIcon className="w-5 h-5 text-primary-600" />
              <div>
                <p className="text-sm text-gray-500"></p>
                <p className="font-medium">{court.name}</p>
              </div>
            </div>

            <div className="flex items-center gap-3">
              <CalendarDaysIcon className="w-5 h-5 text-primary-600" />
              <div>
                <p className="text-sm text-gray-500"></p>
                <p className="font-medium">
                  {new Date(date).toLocaleDateString('zh-TW', {
                    year: 'numeric',
                    month: 'long',
                    day: 'numeric',
                    weekday: 'long'
                  })}
                </p>
              </div>
            </div>

            <div className="flex items-center gap-3">
              <ClockIcon className="w-5 h-5 text-primary-600" />
              <div>
                <p className="text-sm text-gray-500"></p>
                <p className="font-medium">
                  {formatTime(timeSlot.start)} - {formatTime(timeSlot.end)}
                </p>
              </div>
            </div>

            <div className="flex items-center gap-3">
              <UsersIcon className="w-5 h-5 text-primary-600" />
              <div>
                <p className="text-sm text-gray-500"></p>
                <p className="font-medium">{bookingData.totalPlayers} </p>
              </div>
            </div>

          </div>
        </div>

        {/*  */}
        <div className="bg-white border border-gray-200 rounded-xl p-6">
          <h3 className="text-lg font-semibold text-gray-900 mb-4"></h3>
          
          <div className="space-y-3">
            <div className="flex items-center justify-between p-3 bg-primary-50 rounded-lg">
              <div>
                <p className="font-medium text-primary-900">{bookingData.contactName}</p>
                <p className="text-sm text-primary-700">{bookingData.contactEmail}</p>
                <p className="text-sm text-primary-700">{bookingData.contactPhone}</p>
              </div>
              <span className="text-sm text-primary-600"></span>
            </div>
          </div>
          
        </div>

        {/*  */}
        <div className="bg-white border border-gray-200 rounded-xl p-6">
          <h3 className="text-lg font-semibold text-gray-900 mb-4"></h3>
          <textarea
            value={specialRequests}
            onChange={(e) => setSpecialRequests(e.target.value)}
            className="w-full p-3 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-primary-500 focus:border-transparent"
            rows={3}
            placeholder="..."
            maxLength={500}
          />
          <p className="text-sm text-gray-500 mt-2">
            {specialRequests.length}/500 
          </p>
        </div>

        {/*  -  */}
        {court?.type === 'competition' && soloCourtAvailable && onToggleSoloCourt && (
          <div className="bg-white border border-gray-200 rounded-xl p-6">
            <h3 className="text-lg font-semibold text-gray-900 mb-4"></h3>
            
            <div className="flex items-center justify-between p-4 bg-gray-50 rounded-lg">
              <div className="flex items-center space-x-3">
                <input
                  type="checkbox"
                  id="includeSoloCourt"
                  checked={includeSoloCourt}
                  onChange={(e) => onToggleSoloCourt(e.target.checked)}
                  className="w-4 h-4 text-primary-600 bg-gray-100 border-gray-300 rounded focus:ring-primary-500"
                />
                <label htmlFor="includeSoloCourt" className="text-sm font-medium text-gray-900">
                  
                </label>
              </div>
              <div className="text-right">
                <div className="text-sm text-gray-500"></div>
                <div className="text-lg font-semibold text-primary-600">100 </div>
              </div>
            </div>
            
            <p className="text-xs text-gray-500 mt-2">
              * 
            </p>
          </div>
        )}

        {/*  */}
        {availability && (
          <div className="bg-white border border-gray-200 rounded-xl p-6">
            <h3 className="text-lg font-semibold text-gray-900 mb-4"></h3>
            
            <div className="space-y-3">
              <div className="flex justify-between">
                <span className="text-gray-600"></span>
                <span className="font-medium">{availability.pricing?.totalPrice || 0} </span>
              </div>
              
              {includeSoloCourt && (
                <div className="flex justify-between">
                  <span className="text-gray-600"></span>
                  <span className="font-medium">100 </span>
                </div>
              )}
              
              <div className="flex justify-between">
                <span className="text-gray-600"></span>
                <span className="font-medium">{calculateDuration()} </span>
              </div>
              
              <div className="border-t border-gray-200 pt-3">
                <div className="flex justify-between text-lg font-semibold">
                  <span></span>
                  <span className="text-primary-600">
                    {(availability.pricing?.totalPrice || 0) + (includeSoloCourt ? 100 : 0)} 
                  </span>
                </div>
              </div>
            </div>
          </div>
        )}

        {/*  */}
        <div className="bg-yellow-50 border border-yellow-200 rounded-xl p-6">
          <div className="flex items-start gap-3">
            <ExclamationTriangleIcon className="w-5 h-5 text-yellow-600 mt-0.5" />
            <div>
              <h4 className="font-semibold text-yellow-800 mb-2"></h4>
              <ul className="text-sm text-yellow-700 space-y-1">
                <li> 15</li>
                <li> 2</li>
                <li> </li>
                <li> </li>
              </ul>
            </div>
          </div>
        </div>
      </div>

      {/*  */}
      {isEditing && (
        <div className="mt-6 flex gap-3">
          <button
            onClick={handleSave}
            className="flex-1 px-4 py-2 bg-green-600 text-white rounded-lg font-medium hover:bg-green-700 transition-colors"
          >
            
          </button>
          <button
            onClick={handleCancel}
            className="flex-1 px-4 py-2 bg-gray-200 text-gray-700 rounded-lg font-medium hover:bg-gray-300 transition-colors"
          >
            
          </button>
        </div>
      )}

      {/*  */}
      <div className="mt-8 flex gap-4">
        <button
          onClick={onReset}
          className="flex-1 bg-gray-200 text-gray-700 hover:bg-gray-300 font-medium py-3 px-6 rounded-lg transition-colors duration-200"
        >
          
        </button>
        
        <button
          onClick={handleSubmit}
          disabled={isSubmitting || !user || isEditing}
          className={`flex-1 font-medium py-3 px-6 rounded-lg transition-colors duration-200 ${
            isSubmitting || !user || isEditing
              ? 'bg-gray-100 text-gray-400 cursor-not-allowed'
              : 'bg-primary-600 text-white hover:bg-primary-700'
          }`}
        >
          {isSubmitting ? '...' : ''}
        </button>
      </div>

      {!user && (
        <div className="mt-4 p-4 bg-red-50 border border-red-200 rounded-lg">
          <p className="text-red-800 text-sm">
            
          </p>
        </div>
      )}

      {/*  -  */}
      {/*  */}

      {/*  Stripe  */}
    </div>
  );
};

export default BookingSummary;
